@import "https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;700&display=swap";
*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  display: inline-block;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}

body {
  min-height: 100vh;
  background-color: #e5e0ff;
  justify-content: center;
  align-items: center;
  font-family: Nunito, sans-serif;
  font-weight: 400;
  line-height: 1.6;
  display: flex;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  height: auto;
  width: 100vw;
  background-color: #fff2f2;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.sidebar {
  height: 100vh;
  background-color: #7286d3;
  flex-direction: column;
  justify-content: space-between;
  font-family: Nunito, sans-serif;
  display: flex;
}

.search-block {
  font-size: 2rem;
  position: relative;
}

.search-block__label {
  width: 100%;
  display: flex;
  position: relative;
}

.search-block__input {
  height: 2rem;
  color: #212121;
  width: 100%;
  border-color: rgba(0, 0, 0, 0);
  outline: none;
  padding: .9rem 2rem .9rem 4.5rem;
  font-family: Nunito, sans-serif;
}

.search-block__input:focus {
  outline: none;
}

.search-block__input::-webkit-input-placeholder {
  font-family: Nunito, sans-serif;
}

.search-block__input::placeholder {
  font-family: Nunito, sans-serif;
}

.search-block__icon {
  color: #727272;
  position: absolute;
  top: 50%;
  left: 10px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.search-block__list {
  width: 100%;
  background-color: #fff;
  border-bottom-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 5rem;
  left: 0;
}

.search-block__btn {
  font-size: 1.6rem;
}

.search-block__link {
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  padding: 1rem;
  font-size: 1.5rem;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.search-block__link:not(:last-child) {
  border-bottom: 1px solid rgba(153, 153, 153, .31);
}

.search-block__link:not(:last-child):last-child {
  margin-bottom: 1rem;
}

.search-block__link:hover {
  background-color: #e5e0ff;
}

.current-condition {
  flex-direction: column;
  flex-grow: 2;
  justify-content: space-around;
  display: flex;
}

.current-condition__img {
  width: 4rem;
  height: 4rem;
  stroke: #fff2f2;
  fill: rgba(0, 0, 0, 0);
  margin-right: 3.5rem;
}

.current-condition__primary {
  flex-direction: column;
  display: flex;
}

.current-condition__temp {
  justify-content: center;
  margin-top: 2rem;
  display: flex;
}

.current-condition__num {
  font-size: 8rem;
  font-weight: 600;
  line-height: 1;
  display: flex;
}

.current-condition__celsius-main {
  width: 4rem;
  height: 4rem;
}

.current-condition__celsius {
  width: 1rem;
  height: 1rem;
  fill: currentColor;
}

.current-condition__fill {
  color: #666;
  align-self: end;
  padding-bottom: 1rem;
  font-size: 1.5rem;
  display: flex;
}

.current-condition__wrap-img {
  background: radial-gradient(circle, rgba(168, 200, 249, .827) 0%, rgba(9, 9, 121, 0) 65%);
  justify-content: center;
  align-items: center;
  display: flex;
}

.current-condition__wrap-img img {
  max-width: 80%;
}

.current-condition__overcoast {
  color: #e5e0ff;
  padding-left: 2rem;
}

.current-condition__item {
  align-items: center;
  font-size: 2rem;
  display: flex;
}

.current-condition__item:not(:last-child) {
  margin-bottom: 1.5rem;
}

.location {
  flex-flow: column wrap;
  order: -1;
  justify-content: center;
  align-items: center;
  display: flex;
}

.location__item:first-child {
  align-items: center;
  display: flex;
}

.location__title {
  font-size: 3.2rem;
  line-height: 1;
}

.location__item:last-child {
  color: #666;
  font-size: 1.5rem;
}

.location__icon {
  color: #666;
  margin-right: 1rem;
}

.weather {
  min-height: 170px;
  flex-direction: column;
  padding: 2rem 0;
  display: flex;
}

.navigation {
  justify-content: space-between;
  margin-bottom: 2rem;
  padding: 0 1rem;
  display: flex;
}

.nav-list {
  align-items: flex-end;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
}

.nav-list__item:not(:last-child) {
  margin-right: 2.5rem;
}

.nav-list__link {
  border-bottom: 2px solid #000;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.nav-list__link.current {
  color: #c8c8c8;
  border-bottom: 2px solid #c8c8c8;
}

.nav-list__link:hover {
  color: #c8c8c8;
  border-color: #c8c8c8;
}

.temp-changer {
  display: flex;
}

.temp-changer__item:not(:last-child) {
  margin-right: 1rem;
}

.weather-lights__items {
  background-color: #fff;
  font-family: Nunito, sans-serif;
}

.weather-lights__time {
  text-align: center;
  font-size: 1.4rem;
  font-weight: 600;
}

.weather-lights__temp {
  text-align: center;
  font-size: 1.4rem;
}

.weather-lights__temp-now {
  margin-right: .5rem;
  font-weight: 600;
}

.weather-lights__temp-fills {
  color: #a4a4a4;
}

.weather-lights-wraper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
}

.highlights {
  min-height: 38rem;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem 1rem;
  display: flex;
}

.highlights__title {
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 600;
}

.detail-list {
  flex-wrap: wrap;
  margin: -.4rem;
  display: flex;
}

.detail-list__item {
  width: calc(33.3333% - 3.8rem);
  height: 12rem;
  background-color: #fff;
  border-radius: 2rem;
  flex-direction: column;
  justify-content: space-between;
  margin: .4rem;
  padding: 1.5rem;
  display: flex;
}

.detail-list__img--large {
  width: auto;
  height: 100px;
  margin: 0 auto;
}

.detail-list__title {
  color: #a4a4a4;
  font-size: 1.5rem;
}

.detail-list__text {
  font-size: 2.7rem;
  font-weight: 600;
}

.detail-list__wind-direction {
  align-items: center;
  display: flex;
}

.detail-list__wind-direction img {
  width: 3rem;
  height: 3rem;
  margin: 0 1rem 0 0;
}

.detail-list__wind-direction--text {
  color: #a4a4a4;
  font-size: 1.2rem;
}

.detail-list__wraper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.detail-list__wraper img {
  width: 5rem;
  height: auto;
}

.detail-list__status {
  letter-spacing: .1rem;
  font-size: 1.2rem;
}

.detail-list__container--middle {
  margin: auto 0;
}

.sunrise-sunset {
  align-items: center;
  display: flex;
}

.sunrise-sunset__img {
  width: 4rem;
  height: 4rem;
  margin: 0 1rem 0 0;
}

.sunrise-sunset__time {
  font-size: 1.7rem;
  font-weight: 600;
}

.air-quality__img {
  align-self: center;
}

.air-quality__text {
  font-size: 1.5rem;
  font-weight: 600;
}

.btn {
  width: 4rem;
  height: 4rem;
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 600;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.btn-icon {
  width: 2rem;
  height: 2rem;
  fill: currentColor;
}

.btn:hover {
  color: #000;
  background-color: #fff;
}

.icon {
  fill: currentColor;
  width: 2rem;
  height: 2.2rem;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper.weather-lights {
  padding-bottom: 8px;
}

.swiper-button-next, .swiper-button-prev {
  z-index: 9;
  fill: #535353;
  opacity: .3;
  cursor: pointer;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.swiper-button-next:hover, .swiper-button-prev:hover {
  opacity: 1;
}

.swiper-button-next {
  right: 2px;
}

.swiper-button-prev {
  left: 2px;
}

.swiper-button-icon {
  width: 2rem;
  height: 2rem;
}

.swiper-button-disabled {
  opacity: 0;
  display: none;
}

.copyright {
  color: #8d8d8d;
  align-self: center;
  margin-top: 15px;
}

.loader {
  width: 10px;
  height: 10px;
  opacity: 1;
  border-radius: 50%;
  transition: all .25s;
  -webkit-animation: 2s linear infinite shadowRolling;
  animation: 2s linear infinite shadowRolling;
  display: flex;
  position: absolute;
  top: 50%;
  left: calc(50% - 100px);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@-webkit-keyframes shadowRolling {
  0% {
    box-shadow: 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  12% {
    box-shadow: 100px 0 #a2a2a2, 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  25% {
    box-shadow: 110px 0 #a2a2a2, 100px 0 #a2a2a2, 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  36% {
    box-shadow: 120px 0 #a2a2a2, 110px 0 #a2a2a2, 100px 0 #a2a2a2, 0 0 rgba(255, 255, 255, 0);
  }

  50% {
    box-shadow: 130px 0 #a2a2a2, 120px 0 #a2a2a2, 110px 0 #a2a2a2, 100px 0 #a2a2a2;
  }

  62% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 130px 0 #a2a2a2, 120px 0 #a2a2a2, 110px 0 #a2a2a2;
  }

  75% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 130px 0 #a2a2a2, 120px 0 #a2a2a2;
  }

  87% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 130px 0 #a2a2a2;
  }

  100% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0);
  }
}

@keyframes shadowRolling {
  0% {
    box-shadow: 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  12% {
    box-shadow: 100px 0 #a2a2a2, 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  25% {
    box-shadow: 110px 0 #a2a2a2, 100px 0 #a2a2a2, 0 0 rgba(255, 255, 255, 0), 0 0 rgba(255, 255, 255, 0);
  }

  36% {
    box-shadow: 120px 0 #a2a2a2, 110px 0 #a2a2a2, 100px 0 #a2a2a2, 0 0 rgba(255, 255, 255, 0);
  }

  50% {
    box-shadow: 130px 0 #a2a2a2, 120px 0 #a2a2a2, 110px 0 #a2a2a2, 100px 0 #a2a2a2;
  }

  62% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 130px 0 #a2a2a2, 120px 0 #a2a2a2, 110px 0 #a2a2a2;
  }

  75% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 130px 0 #a2a2a2, 120px 0 #a2a2a2;
  }

  87% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 130px 0 #a2a2a2;
  }

  100% {
    box-shadow: 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0), 200px 0 rgba(255, 255, 255, 0);
  }
}

.hide {
  opacity: 0;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: none;
}

.current__btn {
  color: #000;
  background-color: #fff;
}

@media only screen and (min-width: 500px) {
  .sidebar {
    min-height: 57.5rem;
    height: auto;
    padding: 4rem 3rem;
  }

  .current-condition {
    margin-top: 4rem;
  }

  .current-condition__primary {
    flex-direction: initial;
    flex-wrap: wrap;
    margin-bottom: 5rem;
  }

  .current-condition__wrap-img {
    width: 50%;
  }

  .current-condition__wrap-img img {
    max-width: 100%;
  }

  .current-condition__temp {
    align-items: flex-end;
    margin-top: 0;
    margin-left: auto;
  }

  .current-condition__overcoast {
    color: #fff;
    padding-left: 0;
  }

  .current-condition__img {
    stroke: #fff;
  }

  .location {
    width: 100%;
  }

  .weather-lights__items img {
    width: 70%;
  }
}

@media only screen and (min-width: 56.25em) {
  .container {
    max-height: 70rem;
    width: 112.5em;
    border-radius: 50px;
    grid-template-rows: auto 2fr;
    grid-template-columns: minmax(350px, 1fr) minmax(500px, 2fr);
    padding: 0;
    display: grid;
    box-shadow: 0 4px 10px 1px rgba(153, 153, 153, .31);
  }

  .detail-list {
    margin: -1rem;
  }

  .detail-list__item {
    width: calc(33.3333% - 5rem);
    margin: 1rem;
  }

  .sidebar {
    background-color: #fff;
    grid-area: 1 / 1 / span 2 / 2;
    padding: 4rem 3rem;
  }

  .weather {
    min-height: 23rem;
    grid-area: 1 / 2 / 2 / 3;
    padding: 4rem 2rem 0;
  }

  .weather-lights__items {
    border-radius: 1.5rem;
    box-shadow: 0 3px 5px rgba(0, 0, 0, .04);
  }

  .weather-lights__items img {
    width: 100%;
  }

  .swiper-slide.weather-lights__items {
    flex-direction: column;
    justify-content: center;
    display: flex;
  }

  .highlights {
    grid-area: 2 / 2 / 3 / 3;
    justify-content: space-between;
    padding: 2rem 2rem 1rem;
  }

  .current-condition {
    margin-top: 0;
  }

  .current-condition__primary {
    flex-direction: column;
    margin-bottom: 0;
  }

  .current-condition__wrap-img {
    width: 100%;
  }

  .current-condition__temp {
    justify-content: initial;
    align-items: initial;
    margin-left: 0;
  }

  .current-condition__overcoast {
    color: #727272;
  }

  .current-condition__img {
    stroke: #727272;
  }

  .location {
    order: initial;
    justify-content: initial;
    align-items: initial;
    margin-top: 2rem;
  }

  .location__icon {
    flex-shrink: 0;
  }

  .location__item:first-child {
    justify-content: initial;
    margin-right: 0;
  }

  .location__item:last-child {
    margin-left: auto;
  }
}

@media screen and (max-height: 650px) {
  .current-condition__wrap-img {
    background: radial-gradient(circle, rgba(168, 200, 249, .827) 0%, rgba(9, 9, 121, 0) 50%);
  }

  .current-condition__wrap-img img {
    width: 60%;
  }
}

@font-face {
  font-family: swiper-icons;
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}

:root {
  --swiper-theme-color: #007aff;
}

